//   persistReducer  auth 
const authPersistConfig = {
  key: "auth", //    
  storage, //   (localStorage   )
  whitelist: ["token"], //  token 
  // transforms: [nullFilterTransform],
};

//  authReducer  persistReducer
const persistedAuthReducer = persistReducer(authPersistConfig, authReducer);

// ' 
export const rootReducer = combineReducers({
  projectsStore: projectsReducer,
  skillsStore: skillsReducer,
  reviewsStore: reviewsReducer,
  auth: persistedAuthReducer, //   auth 
});

//  middleware   persist 
const middleware = (getDefaultMiddleware) =>
  getDefaultMiddleware({
    serializableCheck: {
      ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER], //  persist 
    },
  });

//  store  
export const store = configureStore({
  reducer: rootReducer, //     auth
  middleware,
});

//  persistor   
export const persistor = persistStore(store);
